
import { isScrollBottom } from "@/utils/comm.js";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import XSwitch from 'vux/src/components/x-switch/index.vue'

import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
export default {
components: {
XHeader
},
data(){
return {
record: [],
totalPage: 0,
page: 1,
upLoading: false,
upLoadingComplete: false,
upmore: true,
}
},
created(){

},
destroyed () {
window.onscroll = null;
},
mounted(){
this.$nextTick(()=>{
this.getRecord();
isScrollBottom(this.scrollBottomCB);
})
},
methods: {
async getRecord () {
var page=this.page;
let that = this;
let data = {
plat: 3,
account: this.account,
token: this.token,
page: page
}
const [err, res] = await api.withdrawhistory(data);
if (err) {
this.$vux.toast.text(err.msg);
return;
}

if(res.code==2000){

if (page === 1) {
this.record=res.data.list;
this.totalPage=res.data.page_size;

} else {
let list = this.record
list.push(...res.data.list);
this.record=list;
}
this.showBottom();
}
},
showBottom () {
if (this.page < parseInt(this.totalPage)) {
this.upLoading=false;
this.upmore=true;
} else {
this.upLoadingComplete=true;
this.upLoading=false;
this.upmore=false;
}
},
scrollBottomCB () {
if (this.upLoadingComplete) {
return;
}
this.upmore=false;
this.upLoading=true;
let p = this.page
p++;
this.page=p;
this.getRecord();
},
},
computed: {
...mapGetters(["user", "account", "token", "carpayInfo"])
},
}



